.categories{
    float: center;
    display:flex;
    overflow:auto;
    margin-bottom: 20px;
    justify-content: space-between;
    margin: auto;
    width: 93%;
    
}
.category-button{
    background-color: white;
    border: none;
    margin-right: 10px;
    cursor: pointer;
    padding: 10px 20px;
    border-radius: 10px;
    font-size: 15px;
    width: 50%;
}

.category-button.active {
    background-color: rgb(185, 197, 217);
}

.category-button:hover{
    background-color: rgb(204, 214, 232);
}