header{
    padding: 10px 10px 10px;
    justify-content: space-between;
    margin:auto;
    display: flex;
    font-family: Arial, Helvetica, sans-serif;

}
.profile{
    border-radius: 25px;
    background-color: white;
    padding: 10px 10px;
    display: flex;
    justify-content: left;
    align-items: center;
    height: 30px;
    font-size: 20px;
    cursor: pointer;
    width: 200px;
}
.profile img{
    margin-right: 10px;
}
.cart{
    float: right;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30px;
    cursor: pointer;
}
.cart img{
    width: 60px;
    height: 60px;
}
div{
    display:contents;
}